<template>
  <div>
    <v-overlay :value="loading" style="z-index: 9999999">
      <v-progress-circular indeterminate size="64"></v-progress-circular>
    </v-overlay>
    <Navbar @show-menu="show = true" :user="user" />

    <v-navigation-drawer
      app
      v-if="user.role === 2"
      :expand-on-hover="$vuetify.breakpoint.mdAndUp"
      :permanent="$vuetify.breakpoint.mdAndUp"
      v-model="show"
      width="400px"
    >
      <v-list v-if="!$route.params.mapel">
        <v-list-item>
          <v-list-item-icon>
            <v-icon color="teal">mdi-book-open-page-variant</v-icon>
          </v-list-item-icon>

          <v-list-item-title>
            <h2 class="teal--text">Daftar Pelajaran</h2>
          </v-list-item-title>
          <v-list-item-action v-if="$vuetify.breakpoint.smAndDown">
            <v-btn icon @click="show = false">
              <v-icon>mdi-chevron-left</v-icon>
            </v-btn>
          </v-list-item-action>
        </v-list-item>
        <v-list-item
          link
          v-for="(mapel, i) in allMapel"
          :to="{
            name: 'MateriList',
            params: { mapel: mapel.name.split(' ').join('-') },
          }"
          :key="i"
        >
          <v-list-item-icon>
            <v-icon>mdi-book</v-icon>
          </v-list-item-icon>
          <v-list-item-title>{{ mapel.name }} </v-list-item-title>
        </v-list-item>
      </v-list>

      <v-list v-if="$route.params.mapel">
        <v-list-item>
          <v-list-item-icon>
            <v-icon color="teal">mdi-book-open-page-variant</v-icon>
          </v-list-item-icon>

          <v-list-item-title>
            <h2 class="teal--text text-uppercase">
              {{ mapel.name }}
            </h2>
          </v-list-item-title>
          <v-list-item-action v-if="$vuetify.breakpoint.smAndDown">
            <v-btn icon @click="show = false">
              <v-icon>mdi-chevron-left</v-icon>
            </v-btn>
          </v-list-item-action>
        </v-list-item>
        <v-list-group
          prepend-icon="mdi-school"
          v-for="(bab, j) in mapel.bab"
          :key="j"
        >
          <template v-slot:activator>
            <v-list-item-title dense> {{ bab.babTitle }} </v-list-item-title>
          </template>

          <v-list-group
            no-action
            sub-group
            v-for="(materi, k) in bab.materi"
            :key="k"
          >
            <template v-slot:activator>
              <v-list-item-content>
                <v-list-item-title
                  >{{ k + 1 }}.0 {{ materi.materiTitle }}</v-list-item-title
                >
              </v-list-item-content>
            </template>

            <v-list-item
              dense
              link
              v-for="(subMateri, l) in materi.subMateri"
              :key="l"
              :to="
                subMateri.type === 'materi'
                  ? `/materi/${$route.params.mapel}/${subMateri._id}`
                  : `/quiz/${$route.params.mapel}/${subMateri._id}`
              "
            >
              <v-list-item-icon>
                <v-icon v-if="subMateri.type === 'materi'">mdi-book</v-icon>
                <v-icon v-else>mdi-file-question</v-icon>
              </v-list-item-icon>
              <v-list-item-title
                >{{ k + 1 }}.{{ l + 1 }} {{ subMateri.title }}
              </v-list-item-title>
              <v-list-item-icon v-if="completed.includes(subMateri._id)">
                <v-icon small color="primary">mdi-check-bold</v-icon>
              </v-list-item-icon>
            </v-list-item>
          </v-list-group>
        </v-list-group>
        <v-list-item link :to="`/absen/${$route.params.mapel}`">
          <v-list-item-icon>
            <v-icon>mdi-bell-check</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>Absen disini</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-main style="min-height: 92vh">
      <v-img
        contain
        style="
          position: absolute;
          opacity: 0.08;
          pointer-events: none;
          width: 100%;
          height: 100%;
        "
        :src="require('@/assets/eduRoom.png')"
        alt=""
      />
      <v-container class="mt-4">
        <v-scale-transition leave-absolute origin="left center">
          <router-view
            :user="user"
            v-if="user.role === 1"
            name="guru"
          ></router-view>
          <router-view
            :user="user"
            v-if="user.role === 2"
            :allMapel="allMapel"
            :mapel="mapel"
            :completed="completed"
            name="murid"
          ></router-view>
          <router-view
            :user="user"
            v-if="user.role === 3"
            name="admin"
          ></router-view>
        </v-scale-transition>
      </v-container>
    </v-main>
    <v-footer class="font-weight-medium grey--text">
      <v-col class="text-center" cols="12">
        {{ new Date().getFullYear() }} — <strong>&copy;eduRoom</strong>
        - SMK TI GARUDA NUSANTARA
      </v-col>
    </v-footer>
  </div>
</template>

<script>
import Navbar from "@/components/Navbar";
import { onMounted, ref, computed } from "@vue/composition-api";
export default {
  name: "Home",
  components: {
    Navbar,
  },
  setup(_, { root }) {
    const show = ref(false);
    const allMapel = ref([]);
    const user = ref({});
    const loading = ref(false);
    const materi = ref({});
    const subMateri = ref({});
    const completed = ref([]);
    const getUserProfile = () => {
      loading.value = true;
      root.axios
        .get("user/profile/", {
          headers: {
            Authorization: localStorage.getItem("token"),
          },
        })
        .then(({ data }) => {
          user.value = data;
          root.axios
            .get("user/completed/" + data._id)
            .then(({ data }) => {
              completed.value = data.complete;
            })
            .catch((err) => console.log(err.response));
        })
        .catch((err) => console.log(err.response))
        .then(() => (loading.value = false));
    };
    const mapel = computed(() => {
      return root.$route.params.mapel
        ? allMapel.value.filter(
            (mapel) =>
              mapel.name == root.$route.params.mapel.split("-").join(" ")
          )[0]
        : "";
    });
    const getMapel = () => {
      loading.value = true;
      root.axios
        .get("user/pelajaran/mapel/")
        .then(({ data }) => {
          allMapel.value = data;
        })
        .catch((err) => console.log(err.response))
        .then(() => (loading.value = false));
    };
    const newMateri = (e) => {
      const bab = mapel.value.bab;
      for (const i in bab) {
        for (const j in bab[i].materi) {
          subMateri.value = bab[i].materi[j].subMateri.find(
            (el) => el._id === e
          );
          const currentMateri = bab[i].materi[j].subMateri.some(
            (el) => el._id === e
          );
          if (currentMateri) {
            materi.value = bab[i].materi[j];
          }
          if (subMateri.value) {
            break;
          }
        }
      }
    };
    onMounted(() => {
      getMapel();

      getUserProfile();
      root.bus.$on("loadingStart", () => (loading.value = true));
      root.bus.$on("loadingStop", () => (loading.value = false));
      root.bus.$on("resetCompleted", (e) => {
        completed.value = e;
      });
      root.bus.$on("previous-materi", (e) => {
        newMateri(e);
        const index = materi.value.subMateri.findIndex((x) => x._id === e);
        const next = materi.value.subMateri[index - 1];
        if (next.type === "materi") {
          root.$router.push(`/materi/${root.$route.params.mapel}/${next._id}`);
        } else {
          root.$router.push(`/quiz/${root.$route.params.mapel}/${next._id}`);
        }
      });
      root.bus.$on("next-materi", (e) => {
        newMateri(e);
        const index = materi.value.subMateri.findIndex((x) => x._id === e);
        const next = materi.value.subMateri[index + 1];
        if (next.type === "materi") {
          root.$router.push(`/materi/${root.$route.params.mapel}/${next._id}`);
        } else {
          root.$router.push(`/quiz/${root.$route.params.mapel}/${next._id}`);
        }
      });
    });
    return { show, user, allMapel, loading, mapel, completed };
  },
  computed: {
    permanent() {
      return window.innerWidth < 960 ? false : true;
    },
  },
};
</script>
